import { fetchAndMaybeUnzipText } from '@jbrowse/core/util';
export function parseBed(text) {
    return new Map(text
        .split(/\n|\r\n|\r/)
        .filter(f => !!f || f.startsWith('#'))
        .map(line => {
        const [refName, start, end, name, score, strand] = line.split('\t');
        return [
            name,
            {
                refName,
                start: +start,
                end: +end,
                score: +score,
                name,
                strand: strand === '-' ? -1 : 1,
            },
        ];
    }));
}
export async function readFile(file, opts) {
    return fetchAndMaybeUnzipText(file, opts);
}
export function zip(a, b) {
    return a.map((e, i) => [e, b[i]]);
}
export function parsePAFLine(line) {
    const parts = line.split('\t');
    const extraFields = parts.slice(12);
    const extra = {
        numMatches: +parts[9],
        blockLen: +parts[10],
        mappingQual: +parts[11],
    };
    if (extraFields.length) {
        for (const field of extraFields) {
            const colonIndex = field.indexOf(':');
            extra[field.slice(0, colonIndex)] = field.slice(colonIndex + 3);
        }
    }
    return {
        tname: parts[5],
        tstart: +parts[7],
        tend: +parts[8],
        qname: parts[0],
        qstart: +parts[2],
        qend: +parts[3],
        strand: parts[4] === '-' ? -1 : 1,
        extra,
    };
}
export function flipCigar(cigar) {
    const arr = [];
    for (let i = cigar.length - 2; i >= 0; i -= 2) {
        arr.push(cigar[i]);
        const op = cigar[i + 1];
        if (op === 'D') {
            arr.push('I');
        }
        else if (op === 'I') {
            arr.push('D');
        }
        else {
            arr.push(op);
        }
    }
    return arr;
}
export function swapIndelCigar(cigar) {
    return cigar.replaceAll('D', 'K').replaceAll('I', 'D').replaceAll('K', 'I');
}
